import React, { useEffect, useState } from 'react';
import { useHistory } from "react-router-dom";
import XLSX from 'xlsx';
import { useGroupState } from "../Main/Model/GroupModel";

import { Grid, Modal, Paper, TextField, Button} from '@material-ui/core';
import ClearIcon from '@material-ui/icons/Clear';

interface StudentPopUpParams {
    open: boolean;
	handleClose : () => void;
	type: string;
	students : Array<number>;
	listName : string;
	saveFunc: (students: Array<number>, listName: string) => void;
}

type StudentListType = {
	[className: string]: Array<number>;
}

type OverlappingStudentsType = {
	[key: number]: number; // key : index, value : 
}

const StudentPopUp: React.FC<StudentPopUpParams> = (props) => {
	const [update, forceUpdate] = useState<boolean>(false); // rendering update
	const [students, setStudents] = useState<Array<number | undefined>>([]);
	const [refinedStudents, setRefinedStudents] = useState<Array<number>>([]); // students undefined  
	const [listName, setListName] = useState<string>("");
	const [studentList, setStudentList] = useState<StudentListType>({});
	
	const groupState = useGroupState();
	const history = useHistory();

	useEffect(()=>{
		setStudents(props.students);
		setListName(props.listName);
	},[props]);
    
	function getStudentList(): void {
		const studentListTmp: StudentListType = {};
		groupState.map((list) => {
			studentListTmp[list.className] = list.students;
		});
		setStudentList(studentListTmp);
	}

	function renderStudentList(){
		//   
		const result= [];
		for (const [name, list] of Object.entries(studentList)) {
			result.push(
				<Button onClick={()=>changeStudentList(name, list)}>{name}</Button>
			)
		}
		return result;
	}

	function changeStudentList(name: string, list: Array<number>): void {
		setListName(name);
		setStudents(list);
		forceUpdate(!update);
	}

	// student field

	function changeStudent(e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>, index: number): void {
		// textfield   students 
		const tmp = students;
		if(e.target.value === undefined) {
			//  
			tmp[index] = undefined;
		} else {
			//  
			const number = Number(e.target.value);
			if (isNaN(number) || number === 0) {
				tmp[index] = undefined;
			} else {
				tmp[index] = number;
			}
		}

		initializeHighlight();
		setStudents(tmp);
		forceUpdate(!update);
	}

	function addStudent(): void {
		//   
		const tmp = students;
		tmp.push(undefined);
		setStudents(tmp);
		forceUpdate(!update);
	}

	function deleteStudent(index: number): void {
		const tmp = students;
		tmp.splice(index,1);
		setStudents(tmp);
		forceUpdate(!update);
	}

	// 

	function preProcessingStudentData(): string {
		let returnString = "";
		const refined: Array<number> = [];
        
		if (listName === "") {
			return "  . .";
		}

		const olStudents = getOverlappingStudents();
		if (Object.keys(olStudents).length !== 0) {
			highlightOverlap(olStudents);
			return "     .";
		}

		students.map((student) => {
			if (student === undefined) {
				//  student textfield    
				return;
			} else if (isNaN(Number(student))) {
				//   
				returnString = "  .";
			} else if (student < 1000000000 || student >= 3000000000) {
				//    
				returnString = "   .";
			} else {
				refined.push(student);
			}
		});
		//   tmp undefined .
		setStudents(refined);
		setRefinedStudents(refined);
		return returnString; // 
	}
    
	function highlightOverlap(olStudents: OverlappingStudentsType): void {
		//    
		for(const number in olStudents){ // number : index
			const studentTag = document.getElementById("modal_student" + number);
			if (studentTag) {
				studentTag.setAttribute("style", "color:red;");
			}
		}
	}

	function initializeHighlight(): void {
		// highlight  ()
		const studentsTag = document.getElementsByClassName("modal_students");

		for (const index in studentsTag){
			if(index !== "length") {
				const inputTag = studentsTag[index].children[1].children[0];
				inputTag.setAttribute("style", "");
			}
			else {
				break;
			}
		}
	}

	function getOverlappingStudents(): OverlappingStudentsType {
		const tmp: {[key: number]: number} = {}; // key : , value : index
		const result: OverlappingStudentsType = {}; // key : index, value : 

		students.map((student, index) => {
			if (student === undefined) {
				return;
			} else if (Object.prototype.hasOwnProperty.call(tmp, student)) {
				result[index] = student;
			} else {
				tmp[student] = index;
			}
		});

		return result;
	}

	// 
    
	function saveStudentList(){
		initializeHighlight();
        
		// preProcessingStudentData refinedStudents 
		const errorMessage = preProcessingStudentData();
		if (errorMessage !== "") {
			alert(errorMessage);
			return;
		}

		props.saveFunc(refinedStudents, listName);
	}

	function uploadXlsxFile(e: React.ChangeEvent<HTMLInputElement>): void {
		if (e.target.files === null) {
			return;
		}

		const f = e.target.files[0];
		if (f === undefined) {
			return;
		}

		const check = f.name.slice(f.name.indexOf(".") + 1).toLowerCase();

		if (check !== 'csv' && check !== 'xlsx') {
			alert('.csv, .xlsx   .');
			return;
		}
		const reader = new FileReader();
		reader.onload = (evt) => { // evt = on_file_select event
			if (evt.target === null) {
				return;
			}

			/* Parse data */
			const bstr = evt.target.result;
			const wb = XLSX.read(bstr, {type:'binary'});
			/* Get first worksheet */
			const wsname = wb.SheetNames[0];
			const ws = wb.Sheets[wsname];
			/* Convert array of arrays */
			const data = XLSX.utils.sheet_to_json(ws);
			/* Update state */
			data.forEach(element => {
				const refined = element as { [kind: string]: number };
				students.push(refined['']);
				history.push('/home/setList');
			});
		};

		reader.readAsBinaryString(f);
	}
	// type  contents
    
	const headerContent = () => {
		if(props.type==="set"){
			//   
			return(
				<Grid container item alignItems="center">
					<TextField label=" " required onInput={(e: React.ChangeEvent<HTMLInputElement>)=>setListName(e.target.value)} rows={1} className="modal_input_field" value={listName}></TextField>
					<input type="file" name="student_list_xlsx" onChange={uploadXlsxFile.bind(this)}/>
					<Button className="save_button" onClick={saveStudentList}></Button>
				</Grid>
			);
		}else if(props.type==="get"){
			//   
			return(
				<Grid container item alignItems="center">
					<Button onClick={getStudentList}>  </Button>
					{renderStudentList()}
					<Button className="save_button" onClick={saveStudentList}></Button>
				</Grid>
			);
		}
	}
    

	return(
		<Modal
			open={props.open}
			onClose={props.handleClose}
			aria-labelledby="student-modal"
			aria-describedby="  ."
			className="modal">
			<Paper className="modal_con">
				<Grid container>
					{headerContent()}
					<Grid container item alignItems="center" wrap="wrap">
						{
							students.map((student, index)=>
								<Grid key={student} item className="box_container">
									<Grid item className="box_content">
										<TextField  label={""+(index+1)} 
											InputLabelProps={{ shrink: true }}
											rows={1}
											onChange={(e)=>changeStudent(e, index)} 
											className={"modal_students modal_input_field"}
											id={"modal_student"+index} 
											value={student}
											error={typeof(student)==="string"?true:false}
											helperText=" ."
										>
										</TextField>
									</Grid>
									<Grid item className="box_xbtn">
										<Button onClick={()=>deleteStudent(index)}><ClearIcon/></Button>
									</Grid>
								</Grid>
							)
						}
						<Button className="add_button" onClick={addStudent}> </Button>
					</Grid>
				</Grid>
			</Paper>
		</Modal>
	);
}

export default StudentPopUp;