import 'date-fns';
import React, {useState, useEffect} from 'react';
import { Grid, TextField } from '@material-ui/core';
import DateFnsUtils from '@date-io/date-fns';
import {
	MuiPickersUtilsProvider,
	KeyboardDatePicker,
} from '@material-ui/pickers';
import { MaterialUiPickersDate } from '@material-ui/pickers/typings/date';

interface TimePickerProps {
	startHelperText: string;
	endHelperText: string;
    publishingTime: Date;
    deadline: Date;
    setPublishingTime: React.Dispatch<Date>;
    setDeadline: React.Dispatch<Date>;
}

const TimePicker: React.FC<TimePickerProps> = (props: TimePickerProps) => {
	const [startDate, setStartDate] = useState<Date>();
	const [endDate, setEndDate] = useState<Date>();
	const { startHelperText, endHelperText, publishingTime, deadline, setPublishingTime, setDeadline } = props;

	useEffect(() => {
		setStartDate(new Date(publishingTime));
		setEndDate(new Date(deadline));
	}, []);

	const checkCorrect = (date1: Date, date2: Date): boolean => {
		if(date1.getTime() <= date2.getTime()){ //     
			return true;
		}else if(date1.getFullYear()===date2.getFullYear()){ // second  ,  
			if(date1.getMonth()===date2.getMonth()){
				if(date1.getDay()===date2.getDay()){
					if(date1.getHours()===date2.getHours()){
						if(date1.getMinutes()===date2.getMinutes()){
							return true;
						}else{
							return false;
						}
					}else{
						return false;
					}
				}
			}else{
				return false;
			}
		}else{
			return false;
		}
		return false
	}

	const startDayChange = (date: MaterialUiPickersDate): void => {
		if (date === null)
			return;

		const tmp = new Date(date);

		if (startDate === undefined) {
			alert("    .")
			return
		}
		if (endDate === undefined) {
			alert("    .")
			return
		}

		tmp.setHours(startDate.getHours());
		tmp.setMinutes(startDate.getMinutes());

		if(checkCorrect(tmp, endDate)) {
			setStartDate(tmp);
			setPublishingTime(tmp);
		} else {
			alert("    .");
		}
	}
  
	const startClockChange = (e: React.ChangeEvent<HTMLInputElement>): void => {
		const time = e.target.value.split(':');

		if (startDate === undefined) {
			alert("    .")
			return
		}
		if (endDate === undefined) {
			alert("    .")
			return
		}

		const tmp = new Date(startDate);
		tmp.setHours(Number(time[0]));
		tmp.setMinutes(Number(time[1]));

		if (checkCorrect(tmp,endDate)) {
			setStartDate(tmp);
			setPublishingTime(tmp);
		} else {
			alert("    .");
		}
	}

	const endDayChange = (date: MaterialUiPickersDate): void => {
		if (date === null) 
			return;

		const tmp = new Date(date);

		if (startDate === undefined) {
			alert("    .")
			return
		}
		if (endDate === undefined) {
			alert("    .")
			return
		}

		tmp.setHours(endDate.getHours());
		tmp.setMinutes(endDate.getMinutes());

		if(checkCorrect(startDate,tmp)) {
			setEndDate(tmp);
			setDeadline(tmp);
		} else {
			alert("    .");
		}
	}
  
	const endClockChange = (e: React.ChangeEvent<HTMLInputElement>): void => {
		const time = e.target.value.split(':');

		if (startDate === undefined) {
			alert("    .")
			return
		}
		if (endDate === undefined) {
			alert("    .")
			return
		}

		const tmp = new Date(endDate);
		tmp.setHours(Number(time[0]));
		tmp.setMinutes(Number(time[1]));

		if(checkCorrect(startDate,tmp)){
			setEndDate(tmp);
			setDeadline(tmp);
		}else{
			alert("    .");
		}
	}

	function timeFormat(date: Date): string{
		let hour = "";
		let minutes = "";

		if(date.getHours() < 10) {
			hour = "0" + date.getHours();
		} else {
			hour = String(date.getHours());
		}

		if(date.getMinutes() < 10) {
			minutes = "0" + date.getMinutes();
		} else {
			minutes = String(date.getMinutes());
		}

		return hour + ":" + minutes;
	}

	return (
		<Grid container className="setting_as_row" direction="row">
			{
				(startDate!==undefined && endDate!== undefined)?
					<MuiPickersUtilsProvider utils={DateFnsUtils}>
						<Grid container spacing={1}>
							<Grid item>
								<KeyboardDatePicker
									disableToolbar
									variant="inline"
									format="MM/dd/yyyy"
									id="start_day_picker"
									label="  "
									value={startDate}
									onChange={startDayChange}
									helperText={startHelperText}
									required
									KeyboardButtonProps={{
										'aria-label': 'change date',
									}}
								/>
							</Grid>
							<Grid item>
								<TextField
									id="start_time_picker"
									label=" "
									type="time"
									defaultValue={timeFormat(startDate)}
									onChange={startClockChange}
									required
									InputLabelProps={{
										shrink: true,
									}}
								/>
							</Grid>
						</Grid>
						<Grid container spacing={1}>
							<Grid item>
								<KeyboardDatePicker
									disableToolbar
									variant="inline"
									format="MM/dd/yyyy"
									id="end_day_picker"
									label="  "
									value={endDate}
									onChange={endDayChange}
									helperText={endHelperText}
									required
									KeyboardButtonProps={{
										'aria-label': 'change date',
									}}
								/>
							</Grid>
							<Grid item>
								<TextField
									id="end_time_picker"
									label=" "
									type="time"
									defaultValue={timeFormat(endDate)}
									onChange={endClockChange}
									required
									InputLabelProps={{
										shrink: true,
									}}
								/>
							</Grid>
						</Grid>
					</MuiPickersUtilsProvider>
					:null
			}
		</Grid>
	);
}

export default TimePicker;